import { styled} from '@stitches/react';
import backgroundImg from '../assets/images/background.png'
import logo from '../assets/images/logo1.png'
import {Link} from "react-router-dom";
import Typography from '@mui/material/Typography';
import MenuBookIcon from '@mui/icons-material/MenuBook';

function Regulation() {
  return (
    <Container>
      <Image src={logo} />
      <Typography component="h1" color="white" variant="h5">
           <MenuBookIcon/> Regulamento
      </Typography>
      <Paragraph>REGULAMENTO SORTE FLASH O que   O Bilhete do SORTE FLASH  um instrumento de ao social, que busca na sociedade o apoio, atravs da aquisio de bilhetes, para custear sua misso filantrpica. Da modalidade  O concurso de prognstico Bilhete do SORTE FLASH, consiste no sorteio contnuo ao acaso de adquirentes dos bilhetes emitidos eletronicamente ou sistema grfico, onde viro j impressas quinze dezenas, geradas num sistema randmico, entre os nmeros de 01 a 75, cujos sorteios ocorrero pela extrao de bolas, igualmente numeradas de 01 a 75, atravs do globo sorteador. Os Sorteios sero transmitidos em tempo real em LIVE NO YOUTUBE. Este projeto est protegido contra cpias e usos sem licena de autoria, conforme Lei 9.279/96 e Lei 9.609/98. Da Legalidade  A iniciativa est respaldada pelas Leis Federais 13.019 e 13.204, Art. 84B. O adquirente deste bilhete social SORTE FLASH, estar contribuindo com amarebrasil.org.br. O Sorteio  continuo, do 1 ao 5 prmio, ou seja, o adquirente concorre as todos os prmios com um nico bilhete, as bolas sero extradas do globo at surgir ganhador nas seguintes legendas. SUPER GIRO FLASH -  sorteado pelo nmero srie do bilhete no sistema Sorte Flash. KUADRA - O primeiro a marcar 4 dezenas em uma das 3 linhas horizontais ganha o prmio. KINA - O primeiro a marcar 5 dezenas em uma das 3 linhas horizontais ganha o prmio. CRUZADINHA - O primeiro a marcar 7 dezenas na cruz no centro do bilhete ganha o prmio. CHEIA - O primeiro a marcar 15 dezenas ganha o prmio. FINA CHEIA -  composto por 14 dezenas, ou seja, faltando um nmero na cartela cheia. GIRO FLASH -  sorteado pelo nmero srie do bilhete no sistema Sorte Flash. Da Premiao - Em caso de dois ou mais ganhadores os prmios sero divididos em partes iguais, prazo de validade do bilhete  90 dias aps extrao sorteada, o detentor do bilhete premiado recebe o prmio na regional responsvel mediante conta bancaria do titular do bilhete, o ganhador dever estar munido do bilhete premiado, sem rasuras, documentos pessoais, RG, CPF e comprovante de endereo de acordo com cadastro do bilhete premiado no site. Caso o prmio no seja resgatado em 90 dias, ser doado para amarebrasil.org.br A ntegra do regulamento desta iniciativa esta disponvel em sorteflash.com PROIBIDA A COMERCIALIZAO PARA MENORES DE 18 ANOS.
    </Paragraph>
    </Container>)
}

const Container = styled('div', {
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  padding: 0,
  margging: 0,
  width: '100%',
  height: '100%',
  backgroundImage: `url(${backgroundImg})`
});

const Button = styled(Link, {
  display: 'flex',
  justifyContent: 'space-evenly',
  alignItems: 'center',
  marginTop: '1em',
  padding: '12px 24px',
  fontSize: 18,
  borderRadius: 24,
  border: 'none',
  cursor: 'pointer',
  backgroundColor: '#228aff',
  color: 'white',
  boxShadow: '4px 4px 6px black',
  width: 256,
  textDecoration: 'none',
  fontWeight: '400',

  variants: {
    color: {
      success: { backgroundColor: '#09cc8e'},
      danger:  { backgroundColor: '#ff223e'},
    },
  },
});
const Image = styled('img', {});

const Paragraph = styled('p', {
  color: 'white',
  fontSize: 13,
  maxWidth: '80%',
  textAlign: 'justify',
  textJustify: 'inter-word',
  padding: 24,
  backgroundColor: 'black',
  borderRadius: 6,
  scrollBehavior:'smooth',
  overflowY: 'scroll'
});



export default Regulation